.container{
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;
}

@media screen and(min-width: 320px) {
    .container{
        width: 320px;
    }
    
}

@media screen and(min-width: 768px) {
    .container{
        padding-left: 32px;
        padding-right: 32px;
        width: 768px;
    }
    
}

@media screen and(min-width: 1280px) {
    .container{
        width: 1280px;
    }
    
}